import { isOneOf, isString, storage } from '@guardian/libs';
const orderByTypes = ['newest', 'oldest', 'recommendations'];
const threadTypes = ['collapsed', 'expanded', 'unthreaded'];
const pageSizeTypes = [25, 50, 100];
const getOrderByType = (value) => isString(value) && isOneOf(orderByTypes)(value) ? value : 'newest';
const getThreadType = (value) => isString(value) && isOneOf(threadTypes)(value) ? value : 'collapsed';
const getPageSizeType = (value) => typeof value === 'number' && isOneOf(pageSizeTypes)(value) ? value : 25;
/** Retrieves stored values from local storage if available, otherwise it returns defaults */
export const initFiltersFromLocalStorage = () => {
    const orderBy = getOrderByType(storage.local.get('gu.prefs.discussion.order'));
    const threads = getThreadType(storage.local.get('gu.prefs.discussion.threading'));
    const pageSize = getPageSizeType(storage.local.get('gu.prefs.discussion.pagesize'));
    return {
        orderBy,
        threads,
        pageSize,
    };
};
